import React, { ReactChild } from 'react';
import { InjectedFormProps, reduxForm } from 'redux-form';

import { Flex } from '@rebass/grid';
import * as H from 'history';
import styled from 'theme';

import { Button } from 'components';
import { basePath, cookiesExpiresConst, formNamesConst, uiItemsConst } from 'consts';
import { TSetIsAccessibleFiltering, TStopAutoRefresh } from 'store';
import { cookiesUtil, storageUtil } from 'utils';

interface IFilterWrapper {
  isHidden: boolean;
}

const FilterWrapper = styled.div<IFilterWrapper>`
  margin-bottom: 15px;
  padding: 10px 15px 12px;
  border: 1px solid ${({ theme }) => theme.colors.lighterGray};
  border-radius: 2px;
  background-color: ${({ theme }) => theme.colors.lighterGrayCell};

  .title {
    font-size: 18px;
    color: ${({ theme, color }) => color || theme.colors.darkGray};
    font-weight: bold;
  }

  ${({ isHidden }) => isHidden && `
    display: none;
  `};
`;

interface IFilter {
  filterAction: () => void;
  filterValues: object;
  isAutoRefresh: boolean;
  isHidden: boolean;
  isLoading: boolean;
  location: H.Location;
  setIsAccessibleFiltering: TSetIsAccessibleFiltering;
  stopAutoRefresh: TStopAutoRefresh;
  FilterForm: ReactChild;
}

export const filteredFieldsToStore = (data: object) => {
  return data && Object.keys(data)
    .filter(key => !key.match(/dateFrom|dateTo|dateTimeFrom|dateTimeTo/gi))
    .reduce(
      (obj, key) => {
        obj[key] = data[key];
        return obj;
      },
      {}
    );
};

type TFilter = IFilter & InjectedFormProps<{}, IFilter>;

const Filter: React.FC<TFilter> = ({
  FilterForm,
  filterAction,
  filterValues,
  handleSubmit,
  isAutoRefresh,
  isHidden,
  isLoading,
  location,
  setIsAccessibleFiltering,
  stopAutoRefresh,
}) => {
  const username = React.useMemo(
    () => {
      const userData = storageUtil.getUserData();
      return userData && userData.username;
    },
    []
  );

  const valuesCount = React.useMemo(
    () => filterValues
      && Object.values(filterValues).reduce((acc, curr) => curr ? ++acc : acc, 0),
    [filterValues]
  );

  const isAccessibleButton = React.useMemo(
    () => {
      const hasInstitution = filterValues && filterValues['institutionId'];
      const hasTransactionId = filterValues && filterValues['transactionId'];
      const hasAccountId = filterValues && filterValues['accountId'];
      const hasCustomerId = filterValues && filterValues['customerId'];
      const hasCardId = filterValues && filterValues['cardId'];
      const hasProductName = filterValues && filterValues['productName'];
      const hasAccountAlias = filterValues && filterValues['accountAlias'];
      const hasPanAlias = filterValues && filterValues['panAlias'];
      const hasLastName = filterValues && filterValues['lastName'];

      const conditions = {
        default: valuesCount > 0,
        [`${basePath}${uiItemsConst.SYSTEM_PROPERTIES}`]: valuesCount >= 0,
        [`${basePath}${uiItemsConst.USERS}`]: valuesCount >= 0,
        [`${basePath}${uiItemsConst.SCHEDULER}`]: valuesCount >= 0,
        [`${basePath}${uiItemsConst.API_CALLS}`]: valuesCount > 1,
        [`${basePath}${uiItemsConst.USERS_ACTIVITY}`]: valuesCount > 1,
        [`${basePath}${uiItemsConst.ACCOUNTS}`]: hasInstitution
          && (hasAccountId || hasAccountAlias || hasLastName),
        [`${basePath}${uiItemsConst.CARDS}`]: hasInstitution
          && (hasAccountId || hasCardId || hasCustomerId || hasPanAlias),
        [`${basePath}${uiItemsConst.STATEMENTS}`]: hasInstitution
          && (hasAccountId || hasAccountAlias || hasLastName),
        [`${basePath}${uiItemsConst.CUSTOMERS}`]: hasInstitution && (hasCustomerId || hasLastName),
        [`${basePath}${uiItemsConst.TRANSACTIONS}`]: hasInstitution
          && (hasTransactionId || hasProductName || hasCustomerId || hasAccountId),
      };

      return location.pathname in conditions ? conditions[location.pathname] : conditions.default;
    },
    [valuesCount, filterValues, location]
  );

  React.useEffect(
    () => {
      setIsAccessibleFiltering(isAccessibleButton);
    },
    [isAccessibleButton, setIsAccessibleFiltering]
  );

  // eslint-disable-next-line
  const handleSubmitForm = React.useCallback(
    handleSubmit(async data => {
      await filterAction();

      if (isAutoRefresh) {
        stopAutoRefresh();
      }

      cookiesUtil.set(
        `${location.pathname}-${username}`,
        JSON.stringify(filteredFieldsToStore(data)),
        { expires: cookiesExpiresConst.MONTH }
      );
    }),
    [handleSubmit, filterAction, isAutoRefresh, stopAutoRefresh]
  );

  return (
    <FilterWrapper isHidden={isHidden}>
      <div className="title">Filter</div>
      <form onSubmit={handleSubmitForm}>
        <Flex
          width="960px"
          alignItems="flex-end"
          flexWrap="wrap"
          m="0 -8px 5px"
        >
          {FilterForm}
        </Flex>
        <Button
          text="Show"
          isLoading={isLoading}
          disabled={!isAccessibleButton}
        />
      </form>
    </FilterWrapper >
  );
};

export default reduxForm<{}, IFilter>({
  form: formNamesConst.FILTER,
  // keepDirtyOnReinitialize: true,
  destroyOnUnmount: false,
  enableReinitialize: true,
})(Filter);
